/*--------------------
Navigation Bar Styling
--------------------*/
.top-nav {
    position: fixed;
    display: block;
    z-index: 3;
    top: 0;
    left: 0%;
    background-color: white;
    .top-nav-logo {
        font-size: 3.1vh;
        margin: .1em;
        color: white;
        h1 {
            font-size: inherit;
        }
        .top-nav-background {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
        }
    }
    &.show {
        left: 0;
    }
}

/*--------------------
Drop down menu styling
--------------------*/
.top-nav {
    display: flex;
    flex-direction: row;
}
.top-nav-logo-div {
    h1 {

    }
}
.top-nav-menu-div {
    .menu-header-menu-container {
        .menu {
            display: flex;
            flex-direction: row;

            .menu-item {
                position: relative;
                overflow: hidden;
                height: 1rem;
                padding: 1rem;
            }
            
            .sub-menu {
                position: relative;
                overflow: hidden;
                top: 0;
                left: 0;
                z-index: 4;
                ul {
                    position: absolute;
                    top: -100%;
                    left: 8px;
                }
            }
        }
    }
}
.show-sub-menu {
    top: 0;
}